body {
  margin: 0;
  padding: 0 !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.blue {color: #06538b;}

.word-wrap{word-wrap: break-word;}

.divider {
  height: 1px;
  width:100%;
  display:block;
  margin: 9px 0;
  overflow: hidden;
  background-color: #e5e5e5;
}

.image {
    cursor: pointer;
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.middle {
    cursor: pointer;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.title {
    line-height: 1.05;
    font-weight: 600 !important;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.author-nick {
    color: #606060;
}

.film-views {
    line-height: 0.6;
    color: #606060;
}


.button-my{
    margin:1px;
}

.button-my:focus {
    outline: none !important;
    outline-offset: none !important;
}


.playlist-add-icon {
    cursor: pointer;
}

.playlist-add-icon-holder {
    cursor: pointer;
}

.centerd {
    display:table-cell;
    vertical-align:middle;
    text-align:center;
}


@media (hover: hover) {

    .container:hover .image {
        opacity: 0.5;
    }

    .container:hover .middle {
        opacity: 1;
    }

    .playlist-add-icon {
        -webkit-transition: .2s ease;
        transition: .2s ease;
        opacity: 0;
    }

    .film-preview-holder:hover .playlist-add-icon {
        opacity: 0.6;
    }

    .film-preview-holder:hover .playlist-add-icon-holder:hover .playlist-add-icon{
        opacity: 1;
    }

    .playlist-remove-holder {
        opacity: 0;
        -webkit-transition: all .2s ease-in-out;
        transition: all .2s ease-in-out;
        color: #606060;
    }



    .playlist-remove-container:hover .playlist-remove-holder  {
        opacity: 0.6;
    }

    .playlist-remove-container:hover .playlist-remove-holder:hover  {
        opacity: 1;
        cursor: pointer;
    }

}

@media (hover: none) {
    .playlist-remove-holder {
        opacity: 0.8;
        color: #606060;
    }

    .playlist-add-icon {
        opacity: 0.7;

    }
}

.dropdown-center-playlist {
    left: 50% !important;
    -webkit-transform: translateX(-50%) !important;
            transform: translateX(-50%) !important;
}

.no-copy {
    -webkit-user-select: none;  /* Chrome all / Safari all */
    -moz-user-select: none;     /* Firefox all */
    -ms-user-select: none;      /* IE 10+ */
    user-select: none;          /* Likely future */
}

.bar {
    background-color: #f8f8f8;
    height: 60px;
}
.dropdown-bar {
    height: 200px;
    width: 400px;
    margin: 0 auto 50px;
    box-shadow:     0      10px      10px       #666;
    /*              |        |         |
                x-shift      |         |
                          y-shift      |
                                 size of shadow
    */
}

.add-item-opacity {
    background-color: rgba(0, 0, 0, 0.4);
}

.add-item-middle {
    cursor: pointer;
    opacity: 0.8;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

mark{
    padding: 0 !important;
    background-color: transparent !important;
    color: black;
    font-weight: 700;
}

#async_typeahead > li > a {
    white-space: normal;
    white-space: initial;

}

@media(max-width:586px){
    .search-bar {
        width: 100%;
    }
}
.custom-avatar {
    margin: 5px;
    cursor: pointer;
}
.container{
    cursor: pointer;
}



.playlist-header {
    background-color: rgba(233, 236, 239, 0.80);

}

.playlist-container {
    background-color: rgba(243, 246, 249, 0.88);

}

.playlist-index {
    color: hsla(0, 0%, 6.7%, .6);
}

.my-center {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.my-center-vertically {
    position:absolute;
    top: 50%;
    left: 90%;
    -webkit-transform: translate(-100%, -50%);
            transform: translate(-100%, -50%);
    -ms-transform: translate(-100%, -50%);
    text-align: center;
}



.title-search {
    font-size: 1.6rem;
    line-height: 1.05;
    font-weight: 400 !important;
}

.film-views-search {
    color: #606060;
}

.filter-search {
    color: #606060;
    cursor: pointer;
}

.style-search {
    cursor: pointer;
}

.inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.inputfile + label {
    font-weight: 600;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    display: inline;
    overflow: hidden;
}

.no-js .inputfile + label {
    display: none;
}

.inputfile + label:hover {
    color: #3ba8c5;
}

.box.has-advanced-upload {
    background-color: #e9ecef;
    border-radius: .3rem;
}
.box.has-advanced-upload .box__dragndrop {
    display: inline;
}

